body {
  max-width: 1920px;
  background-color: #fff;
  width: 100%;
  font-size: 16px;
  justify-content: center;
}

.swiper__hero {
  position: relative;
}
.img-slide__mob {
  display: none;
}
.wrap__slide {
  width: 100%;
  position: absolute;
  z-index: 1;
  display: flex;
  justify-content: space-between;
}
.img__left-slide {
  position: relative;
}
.wrap__img {
  position: relative;
}
.wrap__rigth-slide,
.wrap__left-slide {
  display: block;
  position: relative;
  background-position: left;
}
.wrap__rigth-slide {
  background-position: right;
}
.img-slide__desc {
  position: relative;
}
.title__right {
  top: 0%;
  padding-left: 0%;
  font-size: 130px;
  line-height: 0.5;
  width: 100%;
  height: 100%;
  padding-top: 300px;
  font-family: 'Fiver';
  background-color: rgba(47, 48, 58, 0.4);
  color: #fff;
  font-weight: 400;
  text-transform: uppercase;
  position: absolute;
}

.title__left {
  /* @include font(400, 130px, 1.5, null);*/
  top: 0;
  font-size: 130px;
  width: 100%;
  height: 100%;
  line-height: 0.5;
  padding-top: 300px;
  font-family: 'Fiver';
  background-color: rgba(47, 48, 58, 0.4);
  color: #fff;
  font-weight: 400;
  text-transform: uppercase;
  position: absolute;
  text-align: right;
}
.logo {
  position: absolute;
  top: 20px;
  left: 20px;
  z-index: 3;
}
